.page-wrapper {
  display: flex;
  padding: 30px;
  justify-content: stretch;
}

h1 {
  padding: 15px 30px 0;
  text-align: center;
  display: block;
}

.left-page {
  flex-basis: 30%;
  flex-grow: 0;
}

.no-margin-top {
  margin-top: 0;
}


body {
  background-color: #ddb100;
}

.close {
  cursor: pointer;
}

input, select, button {
  border-radius: 5px;
  line-height: 30px;
  height: 30px;
  font-size: 14px;
  padding: 0 10px;
  border: 2px solid black;
  background-color: white;
  margin-right: 10px;
}

select {
  height: 34px;
}

button {
  background-color: black;
  color: white;
  cursor: pointer;
  height: auto;
}
